@media (min-width: 992px){
    .myRow{
        display: flex;
        flex-direction: row;
        justify-content: center;
        background-color: whitesmoke;
        padding: 2%;
    }
}

.myRow{
    background-color: whitesmoke;
    padding: 2%;
    text-align: center;
}

.centerImg{
    display: flex;
    margin-left: auto;
    margin-right: auto;
    height: 500px;
    width: 500px;
}

.heroImg{
    display: flex;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    height: auto;
}

.frontImg{
    height: 300px;
    width: auto;
    display: flex;
    margin-left: auto;
    margin-right: auto;
}

.profileImg{
    height: 250px;
    width: auto;
    display: flex;
}

.navbar, 
.open, 
.dropdown-menu{
   background-color: #B00532 !important;
   text-align: center;
}

.navbar-nav{
    justify-content: space-evenly !important;
}

.nav-link,
.dropdown-item{
    color: whitesmoke !important;
    font-size: x-large;
}

.dropdown-item:hover{
    background-color: #8a0126;
}


p{
    font-size: large;
    padding-left: 5px;
}



span > h2{
    color: #B00532;

}

a{
    text-align: center;
    color: #b00532;
    font-size: large;
}

a:hover{
    background-color: #8a0126;
}

.blogPost{
    display: block;
	width: 90%;
	margin: auto;
	background-color: whitesmoke;
    box-shadow: inset 5px 5px 0px 5px #B00532;
    padding-top: 0%;
    padding-left: 10px;
    padding-right: 0%;
    padding-bottom: 2%;
	text-align: left;
	font-size: 18px;
	font-family: "Open Sans";
}

.blogTopper{
    display: block;
	width: 100%;
	margin: auto;
	background-color: #B00532;
    box-shadow: 0px 8px 0px 0px #B00532;
    padding-top: 1%;
    padding-left: 1%;
    padding-right: 1%;
    padding-bottom: 0;
}

.blogMeat{
    display: block;
	width: 100%;
	margin: auto;
    padding-top: 10px;
    padding-left: 10px;
    padding-right: 1%;
    padding-bottom: 1%;
    background-color: whitesmoke;
}

div > h3{
    opacity: 1;
    margin-top: 0px;
    padding-bottom: 0px;
    margin-bottom: 0px;
    padding-top: 0px;
    color: whitesmoke;
}

div > h5{
    opacity: 1;
    margin-top: 0px;
    padding-bottom: 3px;
    margin-bottom: 0px;
    padding-top: 0px;
    margin-top: 0px;
    color: whitesmoke;
}

.scroll-to-top {
    position: fixed;
    bottom: 0.5rem;
    right: 0.5rem;
    animation: fadeIn 700ms ease-in-out 1s both;
    cursor: pointer;
    height: 100px;
    width: 100px;
  }

h1{
    text-align: center;
    color: #B00532;
}

.player{
    background-color: lightgray;
    text-align: center;
    padding: 3%;
    margin: auto;
}

.playerImg{
    height: 250px;
    display: flex;
    justify-content: center;
    width: auto;
    padding: 5%;
}

.player > h3{
    color: #B00532;
    padding: 2%;
}